import React, { Component } from 'react'
import Upload from './Upload'
import $ from 'jquery'

class UploadApp extends Component{
    constructor(props){
        super(props);
        this.state = {
            picturename:null
        }
        this.HandleSubmitLogin = this.HandleSubmitLogin.bind(this);
    }
    HandleSubmitLogin(e){
        this.setState({
            picturename:e.picturename
        })
        this.serverRequest = $.post("/KTWS/AnalysisPicture", { picturename:e.picturename }, function (data) {
            console.log(data);
        })
    }
    render(){
        return(
            <div>
                <div>
                    <Upload
                        onSubmit={this.HandleSubmitLogin}/>
                </div>
            </div>
        )

    }
}
export default UploadApp;


