import React, { Component } from 'react'

class Upload extends Component{
    constructor(props){
        super(props);
        this.state = {
            picturename:null
        }
        this.HandlePictureNameChange = this.HandlePictureNameChange.bind(this);
        this.HandleSubmit = this.HandleSubmit.bind(this);
    }
    HandlePictureNameChange(e){
        console.log("handle_username");
        this.setState({
            picturename:e.target.value
        })
    }
    HandleSubmit(e){
        console.log("handle_submit");
        if(this.props.onSubmit){
            const { picturename } = this.state
            this.props.onSubmit({ picturename })
        }
        this.setState({ picturename:"" });
    }
    render(){
        return(
            <div>
                <h1>图片名</h1>
                <input placeholder="请输入名" onChange={this.HandlePictureNameChange}/>
                <button onClick={this.HandleSubmit}>登陆</button>
            </div>
        )
    }
}

export default Upload;


